import { input, } from '../DOMPartials/__items.js';
import saveToStorage from './saveToStorage.js';
import renderTodos from './renderTodos.js';

export default (evt) => {
    evt.preventDefault();

    const todos = JSON.parse(localStorage.getItem('todos')) || [];
    const newTodo = {
        value: input.value,
        completed: false
    };

    todos.push(newTodo);

    input.value = '';
    saveToStorage(todos);
    renderTodos(todos);
};