import saveToStorage from './saveToStorage.js';

export default (evt) => {
    const todos = JSON.parse(localStorage.getItem('todos')) || [];
    const todoText = evt.target.nextElementSibling.innerText;
    
    todos.map(todo => {
        if (todo.value === todoText) {
            todo.completed = !todo.completed;
            
            if (todo.completed)  evt.target.nextElementSibling.setAttribute('class', 'completed');
            else evt.target.nextElementSibling.removeAttribute('class');
        } 
    });

    saveToStorage(todos);
};