
import { list } from '../DOMPartials/__items.js';

export default (todos) => {
    list.innerHTML = '';
    let todoCounter = 0;
    
    todos.map(todo => {
        return list.innerHTML += `
            <li class="list-item" data-id="${todoCounter++}">
                <div class="content">
                    <input type="checkbox" id="checkbox"${todo.completed && ' checked'}>
                    <p id="todo-value" class="${todo.completed && 'completed'}">${todo.value}</p>
                </div>
                
                <div class="actions">
                    <i class="fas fa-marker"></i>
                    <i class="fas fa-trash-alt"></i>
                </div>
            </li>
        `;
    });
};